import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ITenant } from './manage-tenant.types';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class RxTenantService {
  private tenantApiUrl = '/api/rx/application/tenant';

  constructor(private httpClient: HttpClient) {}

  createTenant(tenant: ITenant): Observable<any> {
    return this.httpClient.post(this.tenantApiUrl, tenant);
  }

  deleteTenant(tenantName: string): Observable<any> {
    return this.httpClient.delete(`${this.tenantApiUrl}/${tenantName}`);
  }

  editTenant(tenant: ITenant): Observable<any> {
    return this.httpClient.put(`${this.tenantApiUrl}/${tenant.name}`, tenant);
  }

  getTenant(tenantName: string): Observable<ITenant> {
    return this.httpClient.get<ITenant>(`${this.tenantApiUrl}/${tenantName}`);
  }
}
